
import './App.css';
import Header from "./MyComponents/Header";
import {Todos} from "./MyComponents/Todos";
import {Footer} from "./MyComponents/Footer";
import {Addtodo} from "./MyComponents/addtodo";
import {About} from "./MyComponents/About";
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";

import React, { useEffect ,useState } from 'react';

function App() {
  let initodo;
  if(localStorage.getItem("todo")===null)
  initodo = [];
  else
  initodo = JSON.parse(localStorage.getItem("todo"));

  const onDelete = (todos)=>{
    console.log("THis is deleted",todos)
    setTodo(todo.filter((e)=>{
      return e!==todos;
    }))
  
  }

  const [todo, setTodo] = useState(initodo);

  const addToDo = (title, desc)=>{
    let sno;
    if(todo.length===0){
       sno = 0;
    }else
     sno = todo[todo.length-1].sno + 1;
      const Mytodo = {
        sno: sno,
        title: title,
        desc: desc
      }
      setTodo([...todo, Mytodo]);
      console.log("added todo is ", title, desc, sno);
      
     

  }
  useEffect(() => {
    localStorage.setItem("todo",JSON.stringify(todo));
  }, [todo])
  
  return (
    <>
    <Router>
  <Header title="My Todos List" searchbar={true}/>

    <Switch>
          <Route exact path="/" render={()=>{
            return(
              <>
              <Addtodo addToDo={addToDo}/>
              <Todos todos = {todo} onDelete={onDelete}/>
              </>
            )
          }}>
           
          </Route>
          <Route exact path="/about">
            <About />
          </Route>          
        </Switch>
  <Footer/>
  </Router>

</>    
  );
}

export default App;
